@font-face {
  font-family: "Commissioner";
  font-display: swap;
  src: url("../fonts/Commissioner-Bold.woff") format("woff"),url("../fonts/Commissioner-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Commissioner";
  font-display: swap;
  src: url("../fonts/Commissioner-Medium.woff") format("woff"),url("../fonts/Commissioner-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Commissioner";
  font-display: swap;
  src: url("../fonts/Commissioner-Regular.woff") format("woff"),url("../fonts/Commissioner-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Commissioner";
  font-display: swap;
  src: url("../fonts/Commissioner-SemiBold.woff") format("woff"),url("../fonts/Commissioner-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

:root {
  --font-size: 18px;
  --main-font: 'Commissioner', sans-serif;
  --main-color: #000000;
  --container: 1160px;
  --violet: #6A4B99;
  --purpure: #B689BD;
  --light-putpure: #F0E8F4;
  --green: #D0D600;
  --blue: #64C2C8;
  --light-blue: #E8F5F6;
  --bg-color: #fff;
}

html {
  font-size: var(--font-size);
  font-weight: 500;
}

html,
body {
  scrollbar-gutter: stable;
}

body {
  position: relative;
  margin: 0;
  font-family: var(--main-font);
  font-weight: 400;
  line-height: 140%;
  color: var(--main-color);
  background-color: var(--bg-color);
}

body * {
  font-family: var(--main-font);
  font-weight: 400;
  line-height: 140%;
}

body.no-scroll {
  overflow: hidden;
}

main {
  background-color: var(--bg-color);
}

section {
  position: relative;
  z-index: 1;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul,
ol,
li {
  display: block;
  padding: 0;
  margin: 0;
}

strong,
b {
  font-weight: bold;
}

* {
  box-sizing: border-box;
}

* *:focus-visible,
*:focus-visible,
*:active {
  outline: 0;
}

*::before,
*::after {
  box-sizing: border-box;
}

a,
a:hover,
a:active,
a:focus,
button,
input,
button:hover,
button:active,
button:focus,
input:focus,
input:active {
  text-decoration: none;
  outline: none;
}

a {
  color: inherit;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type='number'],
input[type="number"]:hover,
input[type="number"]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

img,
picture {
  display: block;
}

.container {
  position: relative;
  max-width: var(--container);
  width: 100%;
  margin: 0 auto;
}

.pointer {
  cursor: pointer;
}

.upper {
  text-transform: uppercase;
}

._f-space {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

._f-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

._f-wrap {
  display: flex;
  flex-wrap: wrap;
}

._f-column {
  display: flex;
  flex-direction: column;
}

.validate {
  border-color: darkred !important;
}

.main-title {
  color: var(--violet);
  font-size: 72px;
  font-weight: 700;
  line-height: 1.1;
}

.second-title {
  color: var(--violet);
  font-size: 72px;
  font-weight: 700;
  line-height: 1.1;
}

.sticker-title {
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  height: 84px;
  border: 3px dashed var(--violet);
  border-radius: 100px;
}

.sticker-title span {
  height: 78px;
  padding: 10px 30px 0;
  font-size: 72px;
  line-height: 33px;
  font-weight: 700;
  color: #fff;
  border-radius: 100px;
  user-select: none;
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 280px;
  height: 65px;
  color: #242424;
  font-size: var(--font-size);
  font-weight: 600;
  line-height: 140%;
  background-color: var(--green);
  border: 0;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color .3s ease-in-out, color .3s ease-in-out, border-color .3s ease-in-out;
}

.btn:hover {
  color: #fff;
  background-color: var(--violet);
}

.socials {
  display: flex;
  gap: 26px;
}

.form-row {
  position: relative;
  width: 100%;
}

.form-row .msg {
  position: absolute;
  bottom: 0;
  left: 20px;
  display: none;
  font-size: 10px;
  color: red;
  letter-spacing: .1em;
  transform: translateY(calc(100% + 2px));
}

.checkbox-wrap input {
  display: none;
}

.checkbox-wrap input:checked ~ .custom-checkbox::before {
  content: '\2713';
}

.checkbox-wrap .custom-checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 14px;
  cursor: pointer;
}

.checkbox-wrap .custom-checkbox::before {
  content: '';
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  color: #242424;
  border: 1px solid #242424;
}

.custom-photo input {
  display: none;
}

.dots-arrows_wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: max-content;
  margin: 0 auto;
}

.dots-arrows_wrap .slider-dots {
  position: static;
  order: 1;
}

.dots-arrows_wrap .slick-arrow {
  display: flex;
  width: 24px;
  height: 24px;
  cursor: pointer;
}

.dots-arrows_wrap .slick-arrow svg {
  transition: color .5s ease-in-out;
}

.dots-arrows_wrap .slick-arrow:hover svg {
  color: var(--yellow);
}

.dots-arrows_wrap .slick-arrow.arrow-left {
  order: 0;
}

.dots-arrows_wrap .slick-arrow.arrow-right {
  order: 2;
  justify-content: flex-end;
}

.slider-dots {
  display: flex;
  gap: 5px;
  width: max-content;
  margin: 0 auto;
}

.slider-dots li {
  width: 12px;
  height: 12px;
  background-color: #D9D9D9;
  border-radius: 50%;
  cursor: pointer;
}

.slider-dots li.slick-active {
  background-color: var(--yellow);
}

.slider-dots li button {
  font-size: 0;
  background-color: transparent;
  border: 0;
}

.loader::before {
  content: '';
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  background: rgba(255,255,255,0.6) url("../img/icons/spinner.svg") center/100px no-repeat;
}

.disabled {
  pointer-events: none;
  opacity: .7;
}

.price {
  font-family: var(--coromorant);
  font-size: 30px;
  color: var(--yellow);
}

.price.price_old {
  font-size: 26px;
  color: #fff;
  text-decoration: line-through;
}

#back_to_top {
  position: fixed;
  z-index: 2;
  bottom: 30px;
  right: 30px;
  display: none;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 0 10px 1px rgba(255,255,255,0.8);
  cursor: pointer;
}

#back_to_top svg {
  height: 100%;
  margin-top: -3px;
}

.green {
  color: var(--green);
}

.tablet {
  display: none;
}

.mobile {
  display: none;
}

.ico-pencil {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  transform: translateY(7px);
  cursor: pointer;
}

.ico-pencil:hover .svg-ico,
.ico-pencil:focus-visible .svg-ico {
  color: var(--violet);
}

.ico-pencil .svg-ico {
  color: var(--green);
  transition: color .3s ease-in-out;
}

.select {
  position: relative;
  width: 260px;
}

.select.open .select-title {
  border-color: var(--purpure);
}

.select.open .select-title__ico {
  transform: rotate(180deg);
}

.select.open .select-content {
  opacity: 1;
  transform: scale(1);
}

.select.active .select-title {
  color: #fff;
}

.select-title {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  width: 100%;
  height: 45px;
  padding: 0 10px;
  background-color: transparent;
  border: 1px solid #242424;
  border-radius: 5px;
  cursor: pointer;
  transition: border-color .3s ease-in-out;
}

.select-title:hover,
.select-title:focus-visible {
  border-color: var(--violet);
}

.select-title__text {
  color: #242424;
  font-size: 16px;
}

.select-title__ico {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  transition: transform .3s ease-in-out;
}

.select-content {
  position: absolute;
  top: 60px;
  right: 0;
  left: 0;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: 8px 15px 8px 0;
  background-color: rgba(255,255,255,0.7);
  backdrop-filter: blur(2.5px);
  border: 1px solid #242424;
  border-radius: 5px;
  opacity: 0;
  transform: scale(0);
  transition: opacity .3s ease-in-out;
}

.select-content.no-scroll .select-content_wrap {
  overflow-y: hidden;
}

.select-content_wrap {
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: var(--green) var(--violet);
}

.select-content_wrap::-webkit-scrollbar-thumb {
  background: var(--green);
}

.select-content_wrap::-webkit-scrollbar {
  width: 6px;
}

.select-content_wrap::-webkit-scrollbar-track {
  background: var(--violet);
}

.select-content__wrapper {
  display: flex;
  padding: 8px 16px;
  cursor: pointer;
  transition: background-color .1s ease-in-out;
}

.select-content__wrapper:hover {
  background-color: rgba(138,153,168,0.15);
}

.select-content__wrapper input {
  display: none !important;
}

.accordion {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.accordion.columns {
  flex-direction: row;
  gap: 10px 40px;
  flex-wrap: wrap;
}

.accordion.columns .accordion__item {
  width: calc(50% - 20px);
}

.accordion.columns .accordion__heading {
  align-items: flex-start;
}

.accordion__item {
  width: 100%;
  padding: 35px 30px;
  background-color: var(--purpure);
  border-radius: 30px;
}

.accordion__item.open .accordion__heading .ico-wrap {
  transform: rotate(45deg);
}

.accordion__item.open .accordion__submenu {
  display: block;
}

.accordion__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  font-size: 30px;
  line-height: 1.1;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

.accordion__heading .ico-wrap {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background-color: #fff;
  border-radius: 50%;
  transition: transform .3s ease-in-out;
}

.accordion__submenu {
  display: none;
  margin-top: 15px;
  color: #fff;
}

#header {
  width: 100%;
  height: max-content;
  padding: 10px 0 13px;
}

#header .container {
  display: flex;
  align-items: center;
}

.header__logo {
  width: 160px;
  height: 59px;
}

.header__logo img {
  width: 100%;
  height: max-content;
  object-fit: contain;
}

.hamburger-wrap {
  margin-left: auto;
}

.nav__header {
  margin-right: 111px;
}

.header__nav-list {
  display: flex;
  gap: 40px;
}

.header__btn-wrap {
  display: flex;
  gap: 20px;
}

.main-btn {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32px;
  font-weight: 500;
  border-radius: 20px;
  transition: color .3s ease-in-out, border-color .3s ease-in-out, background-color .3s ease-in-out;
}

.main-btn.auth-btn {
  width: 103px;
  color: #242424;
  background-color: transparent;
  border: 2px solid var(--violet);
}

.main-btn.auth-btn:hover,
.main-btn.auth-btn:focus-visible {
  border-color: var(--green);
}

.main-btn.registration-btn {
  width: 167px;
  color: #fff;
  background-color: var(--violet);
  border: 2px solid var(--violet);
}

.main-btn.registration-btn:hover,
.main-btn.registration-btn:focus-visible {
  background-color: var(--green);
  border-color: var(--green);
}

.header__hamburger {
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  cursor: pointer;
}

.header__hamburger.open .header__hamburger-wrap {
  position: relative;
}

.header__hamburger.open .hamburger-ico {
  position: absolute;
  top: 45%;
  width: 18px;
}

.header__hamburger.open .hamburger-ico.line-1 {
  transform: rotate(45deg);
}

.header__hamburger.open .hamburger-ico.line-2 {
  transform: rotate(0);
  opacity: 0;
}

.header__hamburger.open .hamburger-ico.line-3 {
  transform: rotate(-45deg);
}

.header__hamburger-wrap {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 30px;
  height: 16.67px;
}

.hamburger-ico {
  position: relative;
  width: 26px;
  height: 2px;
  background-color: var(--green);
  transform: rotate(180deg);
  transition: transform .3s ease-in-out;
}

.hamburger-ico::after,
.hamburger-ico::before {
  content: '';
  position: absolute;
  top: 0;
  width: 4px;
  height: 2px;
  background-color: var(--green);
  border-radius: 40%;
}

.hamburger-ico::before {
  left: -2px;
}

.hamburger-ico::after {
  right: -2px;
}

.main-hero {
  padding: 60px 0 0;
}

.main-hero .container {
  display: flex;
}

.main-hero__left {
  display: flex;
  flex-direction: column;
  max-width: 580px;
}

.main-hero__title {
  margin-bottom: 30px;
}

.main-hero__title .sticker-title {
  transform: rotate(-2deg);
}

.main-hero__title .sticker-title span {
  background-color: var(--purpure);
}

.main-hero__description {
  margin-bottom: 40px;
}

.main-hero__right {
  position: absolute;
  top: -40px;
  right: -65px;
  max-width: 655px;
  width: 100%;
  pointer-events: none;
}

.main-hero__right img {
  width: 100%;
  height: max-content;
  object-fit: contain;
}

.fast-search {
  padding: 160px 0 140px;
}

.fast-search__sticker {
  position: absolute;
  top: 100px;
  right: 0;
  pointer-events: none;
  user-select: none;
}

.fast-search__sticker img {
  width: 100%;
  height: max-content;
  object-fit: contain;
}

.fast-search__title {
  display: inline-flex;
  flex-wrap: wrap;
  margin-bottom: 30px;
}

.fast-search__title .sticker-title {
  height: 89px;
  transform: rotate(3deg);
}

.fast-search__title .sticker-title span {
  padding-top: 16px;
  height: 83px;
  background-color: var(--blue);
}

.fast-search__description {
  max-width: 755px;
  margin-bottom: 60px;
  font-size: 22px;
}

.fast-search__list-title {
  margin-bottom: 50px;
  font-size: 50px;
  font-weight: bold;
  line-height: 1.1;
  color: var(--violet);
}

.fast-search__list {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
}

.fast-search__list-item {
  position: relative;
  width: calc(33.33% - 26.66px);
  border-radius: 30px;
}

.fast-search__list-item:nth-child(odd) .fast-search__list-item__heading {
  background-color: var(--blue);
}

.fast-search__list-item:nth-child(odd) .fast-search__list-item__description {
  background-color: var(--light-blue);
}

.fast-search__list-item:nth-child(even) .fast-search__list-item__heading {
  background-color: var(--purpure);
}

.fast-search__list-item:nth-child(even) .fast-search__list-item__description {
  background-color: var(--light-putpure);
}

.fast-search__list-item:last-child {
  padding: 40px 31.5px;
  color: #fff;
  background-color: var(--purpure);
}

.fast-search__list-item__heading {
  min-height: 96px;
  padding: 19px 25px;
  text-align: center;
  font-size: 26px;
  line-height: 1.1;
  font-weight: 600;
  color: #fff;
  border-radius: 30px 30px 0 0;
}

.fast-search__list-item__description {
  height: calc(100% - 96px);
  padding: 20px 25px;
  text-align: center;
  border-radius: 0 0 30px 30px;
}

.fast-search__list-item__image {
  position: absolute;
  right: -42px;
  bottom: -39px;
  width: 150px;
  height: 150px;
  pointer-events: none;
  user-select: none;
}

.benefits {
  padding-bottom: 130px;
}

.benefits__title {
  max-width: 778px;
  margin: 0 auto 30px;
  text-align: center;
}

.benefits__description {
  max-width: 834px;
  margin: 0 auto 40px;
  text-align: center;
}

.benefits__list {
  display: flex;
  flex-wrap: wrap;
  margin: 4px 0 0 4px;
}

.benefits__list-item {
  display: flex;
  flex-direction: column;
  width: 33.33%;
  min-height: 328px;
  padding: 25px 20px;
  margin: -4px 0 0 -4px;
  border: 4px dotted var(--purpure);
  border-radius: 30px;
}

.benefits__list-item:last-child {
  padding: 0;
}

.benefits__list-item .svg-ico {
  margin-bottom: 18px;
}

.benefits__list-item__heading {
  margin-bottom: 15px;
  font-size: 26px;
  font-weight: 600;
  line-height: 1.2;
}

.benefits__btn {
  width: 100%;
  height: 100%;
  border-radius: 27px;
}

.subscription {
  padding-bottom: 140px;
}

.subscription .container {
  padding: 60px 50px;
  background: var(--violet) url("../img/main-subscription-bg.png") center/cover no-repeat;
  border-radius: 30px;
}

.subscription__title {
  max-width: 700px;
  margin-bottom: 30px;
  color: #fff;
}

.subscription__description {
  max-width: 700px;
  margin-bottom: 40px;
  color: #fff;
}

.subscription__price {
  margin-bottom: 30px;
  font-size: 22px;
  font-weight: 600;
  line-height: 145%;
  color: #fff;
}

.subscription__price * {
  font-weight: 600;
}

.testimonials_on-main {
  padding-bottom: 140px;
}

.testimonials_on-page {
  padding: 60px 0 140px;
}

.testimonials_on-page .testimonials__title {
  display: flex;
  flex-direction: column;
  gap: 17px;
  max-width: 993px;
  margin-bottom: 30px;
  text-indent: 0;
}

.testimonials_on-page .testimonials__title .sticker-title {
  position: static;
  height: 89px;
  margin: 0 auto;
  transform: none;
}

.testimonials_on-page .testimonials__title .sticker-title span {
  height: 83px;
  padding-top: 16px;
}

.testimonials_on-page .testimonials__description {
  max-width: 840px;
  margin-bottom: 60px;
}

.testimonials_on-page .testimonials__tabs {
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  max-width: 784px;
  margin: 0 auto 50px;
  padding-right: 0;
}

.testimonials_on-page .testimonials__item-row {
  flex-wrap: wrap;
  display: none;
}

.testimonials_on-page .testimonials__item-row:nth-child(1),
.testimonials_on-page .testimonials__item-row:nth-child(2) {
  display: flex;
}

.testimonials_on-page .testimonials__item-more {
  display: block;
}

.testimonials__title {
  position: relative;
  display: flex;
  justify-content: center;
  max-width: 853px;
  text-indent: 313px;
  margin: 0 auto 30px;
  text-align: center;
}

.testimonials__title .sticker-title {
  position: absolute;
  top: 0;
  left: 137px;
  width: max-content;
  height: 94px;
  text-indent: 0;
  transform: matrix(1, -0.05, 0.05, 1, 0, 0);
}

.testimonials__title .sticker-title span {
  width: max-content;
  height: 88px;
  padding-top: 24px;
  background-color: var(--purpure);
}

.testimonials__description {
  max-width: 745px;
  margin: 0 auto 40px;
  text-align: center;
}

.testimonials__tabs {
  display: flex;
  gap: 25px;
  width: calc(100vw - ((100vw - var(--container)) / 2 + 20px));
  padding-right: calc((100vw - var(--container)) / 2 + 20px);
  margin-bottom: 50px;
  overflow-x: scroll;
  scrollbar-width: none;
}

