}

.design-codes__caption-text {
  width: 100%;
  height: 91px;
  padding: 13px 10px;
  font-size: 16px;
  color: rgba(36,36,36,0.7);
  border: 1px solid rgba(36,36,36,0.7);
  border-radius: 5px;
  resize: none;
  scrollbar-width: thin;
  scrollbar-color: var(--green) var(--violet);
}

.design-codes__caption-text::-webkit-scrollbar-thumb {
  background: var(--green);
}

.design-codes__caption-text::-webkit-scrollbar {
  width: 6px;
}

.design-codes__caption-text::-webkit-scrollbar-track {
  background: var(--violet);
}

.design-codes__size-select {
