}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: none;
  background-color: rgba(106,75,153,0.7);
  backdrop-filter: blur(2.5px);
  cursor: pointer;
}

.modal__close,
.modal__back {
  position: absolute;
  top: 14px;
  right: 19px;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  color: var(--yellow);
  cursor: pointer;
}

.modal__close:hover .svg-ico,
.modal__close:focus-visible .svg-ico,
.modal__back:hover .svg-ico,
.modal__back:focus-visible .svg-ico {
  color: var(--green);
}

.modal__close .svg-ico,
.modal__back .svg-ico {
  color: var(--violet);
  transition: color .3s ease-in-out;
}

.modal__back {
  top: 24px;
  right: auto;
  left: 24px;
}

.modal__content {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 11;
  max-width: 540px;
  width: 100%;
  height: max-content;
  margin: auto;
  padding: 30px 90px 40px;
  background-color: #fff;
  border-radius: 30px;
  cursor: default;
}

.modal__content .btn {
  width: 100%;
  height: 50px;
}

.modal__header {
  display: flex;
  flex-direction: column;
  margin: 0 auto 24px;
}

.modal__logo {
  display: block;
  width: 140px;
  height: 52px;
  margin: 0 auto 18px;
}

.modal__logo img {
  width: 100%;
  height: max-content;
  object-fit: contain;
}

.modal__title {
  margin-bottom: 8px;
  text-align: center;
  color: #242424;
  font-size: 22px;
  font-weight: 600;
}

.modal__description {
  margin: 0 auto;
  text-align: center;
  font-size: 18px;
}

.modal__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 30px;
}

.modal__form {
  display: flex;
  flex-direction: column;
  gap: 18px;
  max-width: 360px;
  width: 100%;
}

.modal__form-row {
  position: relative;
  width: 100%;
}

.modal__form-row .msg {
  position: absolute;
  bottom: 0;
  left: 20px;
  display: none;
  font-size: 10px;
  color: red;
  letter-spacing: .1em;
  transform: translateY(calc(100% + 2px));
}

.modal-input,
.modal-textarea {
  width: 100%;
  font-size: 16px;
  color: #242424;
  background-color: transparent;
  border: 1px solid rgba(36,36,36,0.7);
  border-radius: 30px;
  transition: border-color .3s ease-in-out;
}

.modal-input:hover,
.modal-textarea:hover {
  border-color: var(--violet);
}

.modal-input:focus-visible,
.modal-textarea:focus-visible {
  outline: 0;
  border-color: var(--purpure);
}

.modal-input::placeholder,
.modal-textarea::placeholder {
  color: rgba(36,36,36,0.7);
}

.modal-input {
  height: 47px;
  padding: 12px 20px 13px;
}

.modal-textarea {
  height: 100px;
  padding: 16px 20px;
  resize: none;
}

.forgot-pass__btn {
  display: block;
  margin-bottom: 6px;
  font-size: 16px;
  text-decoration: underline;
}

.modal__policy {
  max-width: 349px;
  margin: 0 auto;
  text-align: center;
  font-size: 13px;
  color: rgba(255,255,255,0.7);
}

.modal__policy a {
  text-decoration: underline;
}

.modal__footer {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  padding-top: 18px;
  border-top: 1px solid #242424;
}

.modal__footer-title {
  margin-bottom: 18px;
  text-align: center;
  font-size: 18px;
}

.modal__footer-description {
  margin-bottom: 18px;
  font-size: 16px;
  text-align: center;
}

.modal__footer-link {
  width: 100%;
  margin: 0 auto;
  background: none;
  border: 3px solid var(--green);
}

.modal__footer-link:hover,
.modal__footer-link:focus-visible {
  color: #242424;
  background: none;
  border-color: var(--violet);
}

.modal__socials {
  display: flex;
  justify-content: center;
  gap: 24px;
}

.modal__socials li {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 47px;
  height: 47px;
}

.modal__socials li img {
  width: 100%;
  height: max-content;
  object-fit: contain;
}

